<template>
    <div class='Register'>
        <el-row :gutter="20">
            <el-col :span="7" :offset="7">
                <div class="grid-content bg-purple">
                    <h1 align="center">用户注册界面</h1>
                </div>
            </el-col>
            <el-col :span="10" :offset="6">
                <div class="grid-content bg-purple">

                    <el-form status-icon  ref="ruleForm"
                             :rules='rules'
                             :model='ruleForm'
                             class="demo-ruleForm"
                             @submit.native.prevent
                    >
                        <el-form-item label="" prop="username">
                            <el-input
                                    type="text"
                                    v-model='ruleForm.username'
                                    autocomplete="off"
                                    placeholder='请输入长度在6-20位的用户名'></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="password">
                            <el-input
                                    :type="passwordType"
                                    v-model='ruleForm.password'
                                    autocomplete="off"
                                    placeholder='请输入长度在6-20位的密码'>
                                <i class="el-icon-view el-input__icon" :style="fontstyle" slot="suffix" @click="showPassword"></i>
                            </el-input>
                        </el-form-item>

                        <el-form-item  prop="verifycode">
                            <!-- 注意：prop与input绑定的值一定要一致，否则验证规则中的value会报undefined，因为value即为绑定的input输入值 -->
                            <el-input v-model="ruleForm.verifycode" placeholder="请输入验证码" class="identifyinput"></el-input>
                        </el-form-item>


                        <el-form-item class='identify'>
                            <div class="identifybox">
                                <div @click="refreshCode">
                                    <validate-code :identifyCode="identifyCode"></validate-code>
                                </div>
                                <el-button @click="refreshCode" type='text' class="textbtn">看不清，换一张</el-button>
                            </div>
                        </el-form-item>

                        <el-form-item>
                            <el-button @click="resetForm('ruleForm')">重置</el-button>
                            <el-button type="primary"
                                       @click="submitForm('ruleForm')"
                                       native-type='submit'
                                       :disabled='!this.active'
                            >注册</el-button>
                        </el-form-item>
                    </el-form>
                </div>
            </el-col>
        </el-row>
    </div>
</template>

<script>
    import {mapState,mapMutations} from 'vuex'

    var Base64 = require('js-base64').Base64
    export default {
        name: "Register",
        mounted() {
            // 验证码初始化
            this.identifyCode = ''
            this.makeCode(this.identifyCodes, 4)
            // this.refreshCode()
        },
        data () {
            const validateVerifycode = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error('请输入验证码'))
                } else if (value !== this.identifyCode) {
                    console.log('validateVerifycode:', value)
                    callback(new Error('验证码不正确!'))
                } else {
                    callback()
                    this.active = true;
                }
            }

            let validateUserName = (rule, value, callback) => {
                if (value===''){
                    callback(new Error('用户名不能为空!'))
                } else if(value.length>20||value.length<6) {
                    callback(new Error('用户名长度必须在6~20之间'))
                }else {
                    callback()
                }
            };

            let validatePassWord = (rule, value, callback) => {
                if (value === ''){
                    callback(new Error('密码不能为空!'))
                } else if(value.length>20||value.length<6){
                    callback(new Error('密码长度必须在6~20之间'))
                } else {
                    callback()
                    // this.active = true;
                }
            };


            return{
                fontstyle: {
                },
                identifyCodes: '1234567890',
                identifyCode: '',
                passwordType: 'password',
                active: false,
                ruleForm: {
                    username: '',
                    password: '',
                    verifycode: '',
                },
                rules: {
                    // ruleForm2:{
                        username: [
                            {validator: validateUserName, change: 'blur'}
                        ],
                        password: [
                            {validator: validatePassWord, change: 'blur'}
                        ],
                        verifycode: [
                            { required: true, trigger: 'blur', validator: validateVerifycode }
                        ]
                    // }
                }
            }



        },
        watch: {
            'ruleForm.username':{
                handler(){
                    this.ruleForm.username = this.ruleForm.username.replace(/\s*/g,"");
                }
            },
            'ruleForm.password':{
                handler(){
                    this.ruleForm.password = this.ruleForm.password.replace(/\s*/g,"");
                }
            }
        },
        computed: {
            // ...mapState(['passWord', 'userName']),
        },
        methods: {
            ...mapMutations(['saveToken']),


            showPassword() {
                this.fontstyle === '' ? (this.fontstyle = 'color: red') : (this.fontstyle = '') // 改变密码可见按钮颜色
                this.passwordType === ''
                    ? (this.passwordType = 'password')
                    : (this.passwordType = '')
            },

            // 生成随机数
            randomNum(min, max) {
                return Math.floor(Math.random() * (max - min) + min)
            },
            // 切换验证码
            refreshCode() {
                this.identifyCode = ''
                this.makeCode(this.identifyCodes, 4)
            },
            // 生成四位随机验证码
            makeCode(o, l) {
                for (let i = 0; i < l; i++) {
                    this.identifyCode += this.identifyCodes[
                        this.randomNum(0, this.identifyCodes.length)
                        ]
                }
                console.log(this.identifyCode)
            },

            resetForm (formName) {
                this.$refs[formName].resetFields()
            },
            submitForm (formName) {
                this.$axios.post('/api/user/register',{
                    "username":this.ruleForm.username,
                    "password": Base64.encode(this.ruleForm.password) //前台对密码进行加密
                }).then((res) => {
                    if (res.data.status) {
                        this.$message({
                            message: res.data.msg,
                            type: 'warning'
                        });
                    } else {
                        this.$message({
                            message: res.data.msg,
                            type: 'success'
                        })

                        this.$router.replace('/login')

                    }
                })
            }
        },
    }
</script>

<style scoped>
    .identify{
        padding: 0 80px;
    }

    .Register{
        height: 630px;
    }
    .el-row{
        /*position: relative;*/
        /*margin-top: 0px;*/
    }
    h1{
        margin: 50px auto;
        font-size: 30px;
        font-weight: bold;
    }
    .el-input{
        /*width: 600px;*/
        margin:  0 auto 30px;
    }
    .el-button:nth-child(2){
        margin-left: 90px;
    }

    .identifybox{
        display: flex;
        justify-content: space-between;
        /*margin-top:7px;*/
    }
    .iconstyle{
        color:#409EFF;
    }
    /*.el-form-item{*/
    /*    line-height: 80px;*/
    /*}*/

</style>